import React, { Component } from 'react';
import { BrowserRouter, Route, Switch } from 'react-router-dom';

const App = ({ routes }) => (
  <BrowserRouter>
    <Switch>
      {routes.map(route => (
        <Route
          exact={route.exact}
          path={route.path}
          name={route.name}
          component={route.component}
        />
      ))}
    </Switch>
  </BrowserRouter>
);

export default App;
